body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: rgba(141, 243, 204, 0.075);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.card-container {
  cursor: pointer;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  margin: auto;
  transition: ease-in;
  background-color: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.13);
}
.card-container:hover .card,
.card-container.hover .card {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card-container,
.front,
.back {
  height: 400px;
  width: 600px;
  border-radius: 2%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card {
  transition: all 0.6s ease-in;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
}

.front,
.back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

/* front pane, placed above back */
.front {
  z-index: 2;
  /* for firefox 31 */
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

/* back, initially hidden pane */
.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.question {
  color: #455358;
  font-size: 2.3em;
  padding: 7%;
  padding-top: 20%;
  text-align: center;
}

.answer {
  color: teal;
  font-size: 1.3em;
  padding: 7%;
  padding-top: 20%;
}

.pin {
  color: rgba(200, 10, 10, 1);
  font-size: 0.8em;
  padding-top: 10%;
}

.buttonContainer {
  margin: 90px;
  text-align: center;
}

.listContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.listItemQuestion,
.listItemAnswer {
  height: 150px;
  width: 700px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  margin: 10px;
  background-color: white;
  padding: 20px;
}

.listItemQuestion {
  width: 400px;
}

.termLength,
.titleText {
  font-family: sans-serif;
  margin: 0 auto;
  font-weight: 600;
  margin-left: 11rem;
}

.title {
  display: -webkit-flex;
  display: flex;
  height: 200px;
  background-color: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.titleText {
  padding-top: 25px;
  font-size: 50px;
  margin-left: 1rem;
  /* text-decoration: underline; */
}

.reactLogo {
  height: 100px;
  margin-top: 15px;
  margin-left: 30px;
}

#titleSubText {
  margin-left: 1rem;
  color: #455358;
}

.brand-logo {
  font-family: sans-serif;
  margin: 0 auto;
  font-weight: 600;
  margin-right: 50rem;
}

